.relative-list {
    background: #e8e8e8;
    border-radius: 4px;
    padding: 5px;
    max-width: 100%;
  }
  
  .relative {
    background: #fff;
    box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.15);
    padding: 3px 10px;
    font-size: 12px;
    margin-bottom: 6px;
    border-radius: 3px;
  
    align-items: center;
    justify-content: space-between;
  }